import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  Grid,
  Button,
  Card,
  CardContent,
  CardMedia,
  IconButton,
  Chip,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Tabs,
  Tab,
  Divider,
  CircularProgress,
  Alert,
  Menu,
  MenuItem
} from '@mui/material';
import {
  Edit as EditIcon,
  Delete as DeleteIcon,
  Visibility as ViewIcon,
  MoreVert as MoreIcon,
  AddCircleOutline as AddIcon,
  RefreshOutlined as RefreshIcon
} from '@mui/icons-material';
import { propertyService } from '../services/api';
import { useAuth } from '../contexts/AuthContext';
import { formatDate, formatPrice } from '../utils/format';

const MyPropertiesPage: React.FC = () => {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  
  // State
  const [properties, setProperties] = useState<any[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [tabValue, setTabValue] = useState<number>(0);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState<boolean>(false);
  const [propertyToDelete, setPropertyToDelete] = useState<any>(null);
  const [menuAnchorEl, setMenuAnchorEl] = useState<null | HTMLElement>(null);
  const [selectedPropertyId, setSelectedPropertyId] = useState<number | null>(null);
  const [filteredProperties, setFilteredProperties] = useState<any[]>([]);

  // Tab labels và filter values
  const tabs = [
    { label: 'Tất cả', value: 'all' },
    { label: 'Đang hiển thị', value: 'available' },
    { label: 'Đã bán/Cho thuê', value: 'sold' }
  ];

  // Load properties khi component được mount
  useEffect(() => {
    if (!isAuthenticated) {
      navigate('/login', { state: { redirectUrl: '/quan-ly-tin-dang' } });
      return;
    }
    
    fetchProperties();
  }, [isAuthenticated, navigate]);

  // Fetch properties từ API
  const fetchProperties = async () => {
    setLoading(true);
    setError(null);

    try {
      console.log('Fetching properties with filter: { user_id: "current" }');
      // Use getProperties with a filter for current user's properties
      const response = await propertyService.getProperties({ user_id: 'current' });
      
      console.log('API response:', response);
      
      if (response.success) {
        const responseData = response.data || [];
        console.log('Response data:', responseData);
        
        // Ensure properties is always an array
        if (Array.isArray(responseData)) {
          console.log('Setting properties from array:', responseData);
          setProperties(responseData);
        } else if (responseData && typeof responseData === 'object') {
          // If data is not an array but an object with properties, convert to array
          console.warn('API returned non-array data, attempting to convert:', responseData);
          if ('properties' in responseData && Array.isArray(responseData.properties)) {
            console.log('Setting properties from nested properties array:', responseData.properties);
            setProperties(responseData.properties);
          } else {
            console.error('Cannot convert API response to properties array:', responseData);
            setProperties([]);
            setError('Dữ liệu không đúng định dạng');
          }
        } else {
          console.error('Unexpected API response format:', responseData);
          setProperties([]);
          setError('Dữ liệu không đúng định dạng');
        }
      } else {
        setProperties([]);
        setError(response.message || 'Không thể tải danh sách bất động sản');
      }
    } catch (err: any) {
      console.error('Error fetching properties:', err);
      setProperties([]);
      setError(err.message || 'Có lỗi xảy ra khi tải dữ liệu');
    } finally {
      setLoading(false);
    }
  };

  // Replace the getFilteredProperties function with a useEffect
  useEffect(() => {
    console.log('Filter effect - properties:', properties);
    
    if (!Array.isArray(properties)) {
      console.error('Properties is not an array:', properties);
      setFilteredProperties([]);
      return;
    }
    
    const filterValue = tabs[tabValue].value;
    
    if (filterValue === 'all') {
      console.log('Setting all properties:', properties);
      setFilteredProperties(properties);
    } else {
      const filtered = properties.filter(property => property.status === filterValue);
      console.log('Setting filtered properties:', filtered);
      setFilteredProperties(filtered);
    }
  }, [properties, tabValue]);

  // Xử lý thay đổi tab
  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  // Mở menu context cho property
  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, propertyId: number) => {
    setMenuAnchorEl(event.currentTarget);
    setSelectedPropertyId(propertyId);
  };

  // Đóng menu context
  const handleMenuClose = () => {
    setMenuAnchorEl(null);
    setSelectedPropertyId(null);
  };

  // Mở dialog xác nhận xóa
  const handleDeleteClick = (property: any) => {
    setPropertyToDelete(property);
    setDeleteDialogOpen(true);
    handleMenuClose();
  };

  // Xóa property
  const confirmDelete = async () => {
    if (!propertyToDelete) return;
    
    try {
      const response = await propertyService.deleteProperty(propertyToDelete.id);
      
      if (response.success) {
        // Cập nhật danh sách sau khi xóa
        setProperties(properties.filter(p => p.id !== propertyToDelete.id));
        setDeleteDialogOpen(false);
        setPropertyToDelete(null);
      } else {
        setError(response.message || 'Không thể xóa bất động sản');
      }
    } catch (err: any) {
      console.error('Error deleting property:', err);
      setError(err.message || 'Có lỗi xảy ra khi xóa bất động sản');
    }
  };

  // Định dạng trạng thái
  const getStatusLabel = (status: string) => {
    switch (status) {
      case 'available':
        return { label: 'Đang hiển thị', color: 'success' };
      case 'pending':
        return { label: 'Đang chờ duyệt', color: 'warning' };
      case 'sold':
      case 'rented':
        return { label: 'Đã bán/Cho thuê', color: 'error' };
      case 'expired':
        return { label: 'Hết hạn', color: 'default' };
      case 'rejected':
        return { label: 'Bị từ chối', color: 'error' };
      default:
        return { label: 'Không xác định', color: 'default' };
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '60vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          Quản lý tin đăng
        </Typography>
        
        <Box>
          <Button
            variant="outlined"
            startIcon={<RefreshIcon />}
            onClick={fetchProperties}
            sx={{ mr: 1 }}
          >
            Làm mới
          </Button>
          
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            component={Link}
            to="/dang-tin"
          >
            Đăng tin mới
          </Button>
        </Box>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      <Paper sx={{ mb: 3 }}>
        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
        >
          {tabs.map((tab, index) => (
            <Tab key={index} label={tab.label} />
          ))}
        </Tabs>
      </Paper>

      {/* Danh sách properties */}
      {filteredProperties.length === 0 ? (
        <Paper sx={{ p: 3, textAlign: 'center' }}>
          <Typography variant="body1" color="text.secondary">
            Bạn chưa có tin đăng nào{tabValue > 0 ? ` ở trạng thái ${tabs[tabValue].label.toLowerCase()}` : ''}
          </Typography>
          <Button
            component={Link}
            to="/dang-tin"
            variant="contained"
            startIcon={<AddIcon />}
            sx={{ mt: 2 }}
          >
            Đăng tin ngay
          </Button>
        </Paper>
      ) : (
        <Grid container spacing={3}>
          {filteredProperties.map((property) => {
            const statusInfo = getStatusLabel(property.status);
            
            return (
              <Grid item xs={12} sm={6} md={4} key={property.id}>
                <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                  {/* Thumbnail */}
                  <Box sx={{ position: 'relative' }}>
                    <CardMedia
                      component="img"
                      height="180"
                      image={property.images && property.images.length > 0 
                        ? property.images[0] 
                        : 'https://via.placeholder.com/300x180?text=No+Image'}
                      alt={property.title}
                    />
                    <Chip
                      label={statusInfo.label}
                      color={statusInfo.color as any}
                      size="small"
                      sx={{
                        position: 'absolute',
                        top: 10,
                        right: 10
                      }}
                    />
                  </Box>
                  
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                      <Typography variant="h6" component="div" gutterBottom noWrap>
                        {property.title}
                      </Typography>
                      
                      <IconButton 
                        size="small"
                        onClick={(e) => handleMenuOpen(e, property.id)}
                      >
                        <MoreIcon />
                      </IconButton>
                    </Box>
                    
                    <Typography color="primary" variant="subtitle1">
                      {formatPrice(property.price)}
                    </Typography>
                    
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                      {property.address}
                    </Typography>
                    
                    <Divider sx={{ my: 1 }} />
                    
                    <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                      <Typography variant="body2" color="text.secondary">
                        Đăng ngày: {formatDate(property.created_at)}
                      </Typography>
                      
                      <Typography variant="body2" color="text.secondary">
                        Lượt xem: {property.view_count || 0}
                      </Typography>
                    </Box>
                  </CardContent>
                  
                  <Box sx={{ p: 2, pt: 0, display: 'flex', justifyContent: 'space-between' }}>
                    <Button
                      size="small"
                      startIcon={<ViewIcon />}
                      component={Link}
                      to={`/bat-dong-san/${property.id}`}
                    >
                      Xem
                    </Button>
                    
                    <Button
                      size="small"
                      startIcon={<EditIcon />}
                      component={Link}
                      to={`/chinh-sua-tin/${property.id}`}
                    >
                      Chỉnh sửa
                    </Button>
                  </Box>
                </Card>
              </Grid>
            );
          })}
        </Grid>
      )}

      {/* Menu cho property */}
      <Menu
        anchorEl={menuAnchorEl}
        open={Boolean(menuAnchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem 
          component={Link}
          to={`/bat-dong-san/${selectedPropertyId}`}
          onClick={handleMenuClose}
        >
          <ViewIcon fontSize="small" sx={{ mr: 1 }} />
          Xem chi tiết
        </MenuItem>
        
        <MenuItem 
          component={Link}
          to={`/chinh-sua-tin/${selectedPropertyId}`}
          onClick={handleMenuClose}
        >
          <EditIcon fontSize="small" sx={{ mr: 1 }} />
          Chỉnh sửa
        </MenuItem>
        
        <MenuItem 
          onClick={() => {
            const property = properties.find(p => p.id === selectedPropertyId);
            if (property) {
              handleDeleteClick(property);
            }
          }}
        >
          <DeleteIcon fontSize="small" sx={{ mr: 1 }} />
          Xóa
        </MenuItem>
      </Menu>

      {/* Dialog xác nhận xóa */}
      <Dialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
      >
        <DialogTitle>Xác nhận xóa</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Bạn có chắc chắn muốn xóa bất động sản "{propertyToDelete?.title}"?
            Hành động này không thể hoàn tác.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>
            Hủy
          </Button>
          <Button onClick={confirmDelete} color="error" autoFocus>
            Xóa
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default MyPropertiesPage; 