import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Button,
  Container,
  Menu,
  MenuItem,
  IconButton,
  Avatar,
  Divider,
  ListItemIcon,
  ListItemText,
  useMediaQuery,
  useTheme,
  Drawer,
  List,
  ListItem,
  ListItemButton
} from '@mui/material';
import {
  Person as PersonIcon,
  Menu as MenuIcon,
  Home as HomeIcon,
  Add as AddIcon,
  ListAlt as ListAltIcon,
  Favorite as FavoriteIcon,
  Logout as LogoutIcon,
  Settings as SettingsIcon
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';

const Header = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const navigate = useNavigate();
  const { isAuthenticated, logout, user } = useAuth();
  
  // State for user menu
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const openUserMenu = Boolean(anchorEl);
  
  // State for mobile menu drawer
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  
  // Handle user menu open
  const handleUserMenuClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  
  // Handle user menu close
  const handleCloseUserMenu = () => {
    setAnchorEl(null);
  };
  
  // Handle logout
  const handleLogout = () => {
    logout();
    handleCloseUserMenu();
    navigate('/');
  };
  
  // Toggle mobile menu
  const toggleMobileMenu = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };
  
  // Main navigation items
  const navItems = [
    { text: 'Trang chủ', link: '/' },
    { text: 'Mua bán', link: '/mua-ban' },
    { text: 'Cho thuê', link: '/cho-thue' },
    { text: 'Tin tức', link: '/tin-tuc' },
    { text: 'Liên hệ', link: '/lien-he' }
  ];
  
  return (
    <AppBar position="sticky" color="default" elevation={1}>
      <Container maxWidth="lg">
        <Toolbar disableGutters>
          {/* Logo */}
          <Typography
            variant="h6"
            component={Link}
            to="/"
            sx={{
              mr: 2,
              display: 'flex',
              fontWeight: 700,
              color: 'primary.main',
              textDecoration: 'none',
              flexGrow: { xs: 1, md: 0 }
            }}
          >
            BĐS VIỆT
          </Typography>
          
          {/* Mobile menu toggle */}
          {isMobile && (
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              onClick={toggleMobileMenu}
            >
              <MenuIcon />
            </IconButton>
          )}
          
          {/* Desktop navigation */}
          {!isMobile && (
            <Box sx={{ flexGrow: 1, display: 'flex' }}>
              {navItems.map((item) => (
                <Button
                  key={item.text}
                  component={Link}
                  to={item.link}
                  sx={{ mx: 1, color: 'text.primary' }}
                >
                  {item.text}
                </Button>
              ))}
            </Box>
          )}
          
          {/* Desktop user menu or auth buttons */}
          {!isMobile && (
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {isAuthenticated ? (
                <>
                  <Button
                    variant="contained"
                    startIcon={<AddIcon />}
                    component={Link}
                    to="/dang-tin"
                    sx={{ mr: 2 }}
                  >
                    Đăng tin
                  </Button>
                  
                  <IconButton
                    onClick={handleUserMenuClick}
                    size="small"
                    edge="end"
                    aria-controls={openUserMenu ? 'user-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={openUserMenu ? 'true' : undefined}
                  >
                    <Avatar sx={{ width: 32, height: 32 }}>
                      {user?.name?.charAt(0) || user?.username?.charAt(0) || 'U'}
                    </Avatar>
                  </IconButton>
                </>
              ) : (
                <>
                  <Button
                    component={Link}
                    to="/login"
                    sx={{ mx: 1 }}
                  >
                    Đăng nhập
                  </Button>
                  <Button
                    component={Link}
                    to="/register"
                    variant="contained"
                  >
                    Đăng ký
                  </Button>
                </>
              )}
            </Box>
          )}
          
          {/* User dropdown menu */}
          <Menu
            id="user-menu"
            anchorEl={anchorEl}
            open={openUserMenu}
            onClose={handleCloseUserMenu}
            MenuListProps={{
              'aria-labelledby': 'user-button',
            }}
            transformOrigin={{ horizontal: 'right', vertical: 'top' }}
            anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
          >
            <MenuItem component={Link} to="/ho-so" onClick={handleCloseUserMenu}>
              <ListItemIcon>
                <PersonIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>Hồ sơ cá nhân</ListItemText>
            </MenuItem>
            
            <MenuItem component={Link} to="/quan-ly-tin-dang" onClick={handleCloseUserMenu}>
              <ListItemIcon>
                <ListAltIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>Quản lý tin đăng</ListItemText>
            </MenuItem>
            
            <MenuItem component={Link} to="/yeu-thich" onClick={handleCloseUserMenu}>
              <ListItemIcon>
                <FavoriteIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>Tin đã lưu</ListItemText>
            </MenuItem>
            
            <Divider />
            
            <MenuItem component={Link} to="/cai-dat" onClick={handleCloseUserMenu}>
              <ListItemIcon>
                <SettingsIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>Cài đặt</ListItemText>
            </MenuItem>
            
            <MenuItem onClick={handleLogout}>
              <ListItemIcon>
                <LogoutIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText>Đăng xuất</ListItemText>
            </MenuItem>
          </Menu>
          
          {/* Mobile menu drawer */}
          <Drawer
            anchor="right"
            open={mobileMenuOpen}
            onClose={toggleMobileMenu}
          >
            <Box
              sx={{ width: 250 }}
              role="presentation"
              onClick={toggleMobileMenu}
            >
              <List>
                {navItems.map((item) => (
                  <ListItem key={item.text} disablePadding>
                    <ListItemButton component={Link} to={item.link}>
                      <ListItemText primary={item.text} />
                    </ListItemButton>
                  </ListItem>
                ))}
                
                <Divider />
                
                {isAuthenticated ? (
                  <>
                    <ListItem disablePadding>
                      <ListItemButton component={Link} to="/dang-tin">
                        <ListItemIcon>
                          <AddIcon />
                        </ListItemIcon>
                        <ListItemText primary="Đăng tin" />
                      </ListItemButton>
                    </ListItem>
                    
                    <ListItem disablePadding>
                      <ListItemButton component={Link} to="/ho-so">
                        <ListItemIcon>
                          <PersonIcon />
                        </ListItemIcon>
                        <ListItemText primary="Hồ sơ cá nhân" />
                      </ListItemButton>
                    </ListItem>
                    
                    <ListItem disablePadding>
                      <ListItemButton component={Link} to="/quan-ly-tin-dang">
                        <ListItemIcon>
                          <ListAltIcon />
                        </ListItemIcon>
                        <ListItemText primary="Quản lý tin đăng" />
                      </ListItemButton>
                    </ListItem>
                    
                    <ListItem disablePadding>
                      <ListItemButton component={Link} to="/yeu-thich">
                        <ListItemIcon>
                          <FavoriteIcon />
                        </ListItemIcon>
                        <ListItemText primary="Tin đã lưu" />
                      </ListItemButton>
                    </ListItem>
                    
                    <Divider />
                    
                    <ListItem disablePadding>
                      <ListItemButton onClick={handleLogout}>
                        <ListItemIcon>
                          <LogoutIcon />
                        </ListItemIcon>
                        <ListItemText primary="Đăng xuất" />
                      </ListItemButton>
                    </ListItem>
                  </>
                ) : (
                  <>
                    <ListItem disablePadding>
                      <ListItemButton component={Link} to="/login">
                        <ListItemText primary="Đăng nhập" />
                      </ListItemButton>
                    </ListItem>
                    
                    <ListItem disablePadding>
                      <ListItemButton component={Link} to="/register">
                        <ListItemText primary="Đăng ký" />
                      </ListItemButton>
                    </ListItem>
                  </>
                )}
              </List>
            </Box>
          </Drawer>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header; 