import React, { createContext, useState, useEffect, useContext, ReactNode } from 'react';
import { authService, userService } from '../services/api';

// Định nghĩa kiểu dữ liệu User
export interface User {
  id: number;
  username: string;
  name?: string;
  email: string;
  phone?: string;
  avatar?: string;
  role?: string;
  status?: string;
  created_at?: string;
}

// Helper interface for typechecking response
interface AuthResponse {
  success: boolean;
  data?: {
    user?: User;
    token?: string;
  };
  message?: string;
  user?: User;
}

// Type guard function to check if a value is an AuthResponse
function isAuthResponse(value: any): value is AuthResponse {
  return value !== null && 
         typeof value === 'object' && 
         'success' in value;
}

// Định nghĩa kiểu dữ liệu cho context
interface AuthContextType {
  isAuthenticated: boolean;
  user: User | null;
  loading: boolean;
  error: string | null;
  login: (username: string, password: string) => Promise<void>;
  register: (userData: any) => Promise<void>;
  logout: () => void;
  updateProfile: (data: Partial<User>) => Promise<any>;
  updateAvatar: (formData: FormData) => Promise<any>;
  changePassword: (currentPassword: string, newPassword: string) => Promise<any>;
  setUser: React.Dispatch<React.SetStateAction<User | null>>;
}

// Tạo context với giá trị mặc định
const AuthContext = createContext<AuthContextType>({
  isAuthenticated: false,
  user: null,
  loading: true,
  error: null,
  login: async () => {},
  register: async () => {},
  logout: () => {},
  updateProfile: async () => {},
  updateAvatar: async () => {},
  changePassword: async () => {},
  setUser: () => {},
});

// Custom hook để sử dụng AuthContext
export const useAuth = () => useContext(AuthContext);

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  // Kiểm tra trạng thái đăng nhập khi component được mount
  useEffect(() => {
    const checkAuthStatus = async () => {
      setLoading(true);
      try {
        const token = localStorage.getItem('token');
        const storedUser = localStorage.getItem('user');
        
        // Nếu không có token, xóa trạng thái đăng nhập
        if (!token) {
          setIsAuthenticated(false);
          setUser(null);
          setLoading(false);
          return;
        }

        // Hiển thị dữ liệu user từ localStorage ngay lập tức để tránh màn hình trống
        if (storedUser) {
          try {
            const userData = JSON.parse(storedUser);
            setUser(userData);
            setIsAuthenticated(true);
          } catch (e) {
            console.error('Error parsing stored user data:', e);
          }
        }
        
        // Sau đó mới gửi request xác thực token đến server
        try {
          console.log('Checking auth status with server...');
          const response = await authService.checkAuth();
          
          if (response === false) {
            console.log('Auth check failed: Server returned false');
            setIsAuthenticated(false);
            setUser(null);
            localStorage.removeItem('token');
            localStorage.removeItem('refreshToken');
            localStorage.removeItem('user');
          } 
          else if (isAuthResponse(response) && response.success) {
            console.log('Auth check succeeded');
            setIsAuthenticated(true);
            const userData = response.data?.user || response.user || null;
            
            // Chỉ cập nhật user nếu server trả về dữ liệu
            if (userData) {
              setUser(userData);
              localStorage.setItem('user', JSON.stringify(userData));
            }
          } 
          else {
            console.log('Auth check failed: Unexpected response format');
            // Thất bại nhưng không tự động đăng xuất nếu đã có dữ liệu từ localStorage
            if (!storedUser) {
              setIsAuthenticated(false);
              setUser(null);
              localStorage.removeItem('token');
              localStorage.removeItem('refreshToken');
            }
          }
        } catch (error) {
          console.error('Auth check API error:', error);
          // Thất bại nhưng không tự động đăng xuất nếu đã có dữ liệu từ localStorage
          if (!storedUser) {
            setIsAuthenticated(false);
            setUser(null);
            localStorage.removeItem('token');
            localStorage.removeItem('refreshToken');
          }
        }
      } catch (error) {
        console.error('General auth check error:', error);
        setIsAuthenticated(false);
        setUser(null);
        localStorage.removeItem('token');
        localStorage.removeItem('refreshToken');
        localStorage.removeItem('user');
      } finally {
        setLoading(false);
      }
    };

    checkAuthStatus();
  }, []);

  // Đăng nhập
  const login = async (username: string, password: string) => {
    setLoading(true);
    setError(null);
    try {
      const response = await authService.login(username, password);
      
      if (isAuthResponse(response) && response.success) {
        setIsAuthenticated(true);
        const userData = response.data?.user || response.user || null;
        setUser(userData);
      } else {
        setError(isAuthResponse(response) ? response.message || 'Đăng nhập thất bại' : 'Đăng nhập thất bại');
      }
    } catch (err: any) {
      setError(err.response?.data?.message || 'Đăng nhập thất bại');
    } finally {
      setLoading(false);
    }
  };

  // Đăng ký
  const register = async (userData: any) => {
    setLoading(true);
    setError(null);
    try {
      const response = await authService.register(userData);
      
      if (isAuthResponse(response) && response.success) {
        setIsAuthenticated(true);
        const userData = response.data?.user || response.user || null;
        setUser(userData);
      } else {
        setError(isAuthResponse(response) ? response.message || 'Đăng ký thất bại' : 'Đăng ký thất bại');
      }
    } catch (err: any) {
      setError(err.response?.data?.message || 'Đăng ký thất bại');
    } finally {
      setLoading(false);
    }
  };

  // Đăng xuất
  const logout = () => {
    authService.logout();
    setIsAuthenticated(false);
    setUser(null);
  };

  const updateProfile = async (data: Partial<User>) => {
    try {
      const response = await authService.updateProfile(data);
      
      // Sửa: Lấy trực tiếp từ response, không cần .data
      if (isAuthResponse(response) && (response.user || (response.data && response.data.user))) {
        const updatedUser = response.user || response.data?.user;
        if (updatedUser) {
          setUser(updatedUser);
          localStorage.setItem('user', JSON.stringify(updatedUser));
        }
      }
      
      return response;
    } catch (error) {
      throw error;
    }
  };

  const updateAvatar = async (formData: FormData) => {
    try {
      console.log('Starting avatar update...');
      const response = await userService.updateAvatar(formData);
      console.log('Avatar update successful, response:', response);
      
      // Extract avatar path from response
      let avatarPath;
      if (response && response.data && response.data.avatar) {
        avatarPath = response.data.avatar;
      } else if (response && response.avatar) {
        avatarPath = response.avatar;
      } else {
        console.warn('Could not find avatar path in response.');
        throw new Error('Could not find avatar path in response');
      }
      
      // Update user state with new avatar
      if (user) {
        console.log('Updating user with new avatar path:', avatarPath);
        
        // First update localStorage to ensure persistence
        const currentUser = JSON.parse(localStorage.getItem('user') || '{}');
        currentUser.avatar = avatarPath;
        localStorage.setItem('user', JSON.stringify(currentUser));
        
        // Then update state
        const updatedUser = { ...user, avatar: avatarPath };
        setUser(updatedUser);
        
        // Log updates for debugging
        console.log('User state updated with new avatar');
        console.log('Updated localStorage user:', JSON.parse(localStorage.getItem('user') || '{}'));
      }
      
      return response;
    } catch (error: any) {
      console.error('Avatar update error in AuthContext:', error);
      throw error;
    }
  };

  const changePassword = async (currentPassword: string, newPassword: string) => {
    try {
      const response = await authService.changePassword(currentPassword, newPassword);
      return response;
    } catch (error) {
      throw error;
    }
  };

  // Giá trị của context
  const value = {
    isAuthenticated,
    user,
    loading,
    error,
    login,
    register,
    logout,
    updateProfile,
    updateAvatar,
    changePassword,
    setUser
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export default AuthContext; 